* {
  margin: 0;
  padding: 0;
  list-style: none;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: white;
}

*::before, *::after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
  color: inherit;
}

body {
  font-family: 'Karla', sans-serif;
  background: #333;
}

body h1 {
  font-family: 'Kite One', sans-serif;
  font-weight: 100;
}

.fragmento {
  width: 350px;
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
  pointer-events: none;
  cursor: -webkit-zoom-in;
  cursor: zoom-in;
}

.fragmento *, .fragmento *::before {
  -webkit-transition: all 500ms ease;
  -o-transition: all 500ms ease;
  transition: all 500ms ease;
}

.fragmento > * {
  position: absolute;
}

.fragmento svg {
  min-width: 110%;
}

.fragmento svg polygon {
  vector-effect: non-scaling-stroke;
  stroke: white;
  stroke-width: 1;
  fill: none;
}

.fragmento h1 {
  pointer-events: none;
  opacity: 0;
  -webkit-transform: translateX(30%);
  -ms-transform: translateX(30%);
  transform: translateX(30%);
}

.fragmento h1::before {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  right: 100%;
  margin-right: 15px;
  width: calc( 350px - 150%);
  min-width: 100px;
  max-width: 250px;
  height: 1px;
  background: white;
  -webkit-transform: translateX(60%);
  -ms-transform: translateX(60%);
  transform: translateX(60%);
}

.fragmento .imagen {
  width: 350px;
  height: 350px;
  pointer-events: initial;
}

.fragmento .imagen > div {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  -webkit-filter: brightness(0.3) contrast(0.4);
  filter: brightness(0.3) contrast(0.4);
  -webkit-transform: scale(1.3);
  -ms-transform: scale(1.3);
  transform: scale(1.3);
}

.fragmento:hover .imagen > div {
  -webkit-filter: brightness(0.4) contrast(0.9);
  filter: brightness(0.4) contrast(0.9);
  -webkit-transform: scale(1);
  -ms-transform: scale(1);
  transform: scale(1);
}

.fragmento:hover h1 {
  opacity: 1;
  -webkit-transform: translateX(0);
  -ms-transform: translateX(0);
  transform: translateX(0);
}

.fragmento:hover h1::before {
  -webkit-transform: translateX(0);
  -ms-transform: translateX(0);
  transform: translateX(0);
}

.fragmento:hover svg {
  min-width: 125%;
}

.App {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  cursor: move;
  -webkit-transition: opacity 1s ease;
  -o-transition: opacity 1s ease;
  transition: opacity 1s ease;
  -webkit-transition-delay: 1s;
  -o-transition-delay: 1s;
  transition-delay: 1s;
  position: relative;
}

.App.cargando {
  opacity: 0;
}

.App.cargado {
  opacity: 1;
}

.fondoApp {
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  height: 4000px;
  width: 4000px;
  -webkit-transform: translate(-50%, -50%) rotate(25deg) scale(3);
  -ms-transform: translate(-50%, -50%) rotate(25deg) scale(3);
  transform: translate(-50%, -50%) rotate(25deg) scale(3);
  background-image: -webkit-linear-gradient(left, black 0.5px, transparent 0.5px);
  background-image: -o-linear-gradient(left, black 0.5px, transparent 0.5px);
  background-image: linear-gradient(90deg, black 0.5px, transparent 0.5px);
  background-size: 100px 100%;
  opacity: .19;
}

#contenedor {
  position: relative;
  -webkit-transform: translate(-50%, -50%) scale(0.6);
  -ms-transform: translate(-50%, -50%) scale(0.6);
  transform: translate(-50%, -50%) scale(0.6);
  position: relative;
  -webkit-transition: all 1s ease;
  -o-transition: all 1s ease;
  transition: all 1s ease;
  height: 100%;
  top: 50%;
  left: 50%;
}

#logos {
  position: fixed;
  height: 40px;
  bottom: 10px;
  z-index: 1000;
}

#logos img {
  height: 100%;
  margin: 0 20px;
}

@media (min-width: 1390px) {
  #contenedor {
    -webkit-transform: translate(-50%, -50%) scale(0.8);
    -ms-transform: translate(-50%, -50%) scale(0.8);
    transform: translate(-50%, -50%) scale(0.8);
  }
}

.logo-contenedor {
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
  width: 400px;
  height: 400px;
}

.logo-contenedor > div, .logo-contenedor svg {
  position: absolute;
  height: 100%;
  width: 100%;
}

.logo-contenedor .atras > svg {
  overflow: visible;
}

.logo-contenedor .fondo {
  width: 98%;
  height: 98%;
}

.logo-contenedor .frente, .logo-contenedor .menu {
  width: 88%;
  height: 88%;
}

.logo-contenedor .frente {
  -webkit-transform: translateX(-1.7%);
  -ms-transform: translateX(-1.7%);
  transform: translateX(-1.7%);
  overflow: hidden;
}

.logo-contenedor .frente svg {
  width: 400px;
  height: 400px;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
}

.logo-contenedor .menu {
  padding: 15px;
  font-family: Calibri, sans-serif;
}

.logo-contenedor .menu h1 {
  font-size: 96pt;
  line-height: 0.7em;
  font-family: calibri, sans-serif;
  font-weight: 700;
}

.logo-contenedor .menu h2 {
  font-size: 30pt;
  line-height: 1em;
  margin-top: 100px;
  width: 100%;
  font-weight: 300;
}

#entrada {
  position: fixed;
  top: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  -webkit-transform: scale(0.95) translateY(10%);
  -ms-transform: scale(0.95) translateY(10%);
  transform: scale(0.95) translateY(10%);
  -webkit-transition: all 700ms ease;
  -o-transition: all 700ms ease;
  transition: all 700ms ease;
}

#entrada.abierto {
  opacity: 1;
  -webkit-transform: scale(1) translateY(0);
  -ms-transform: scale(1) translateY(0);
  transform: scale(1) translateY(0);
}

#entrada.abierto .cerrarBtn {
  pointer-events: auto;
}

#entrada .contenido {
  width: 100%;
  height: 100%;
  overflow: hidden;
  -webkit-perspective: 10cm;
  perspective: 10cm;
}

#entrada .contenido > * {
  position: absolute;
}

#entrada .contenido .fondo {
  width: 123%;
  height: 100%;
  background-size: cover;
  background-position: center;
  -webkit-filter: brightness(0.3);
  filter: brightness(0.3);
  -webkit-transform: translateZ(-40cm) scale(5.05);
  transform: translateZ(-40cm) scale(5.05);
}

#entrada .contenido .galeria {
  height: 100%;
  width: 100%;
  top: 0;
  left: 100%;
  background-image: -webkit-gradient(linear, left top, right top, from(transparent), color-stop(70%, #111));
  background-image: -webkit-linear-gradient(left, transparent 0%, #111 70%);
  background-image: -o-linear-gradient(left, transparent 0%, #111 70%);
  background-image: linear-gradient(90deg, transparent 0%, #111 70%);
  display: -ms-flexbox;
  display: flex;
  padding-top: 100px;
  overflow: auto;
}

#entrada .contenido .galeria .fotos {
  text-align: center;
  padding: 0 10%;
}

#entrada .contenido .galeria .fotos img {
  margin: 7px;
  -webkit-filter: brightness(0.5);
  filter: brightness(0.5);
  -webkit-transition: all 500ms ease;
  -o-transition: all 500ms ease;
  transition: all 500ms ease;
}

#entrada .contenido .galeria .fotos img:hover {
  -webkit-filter: brightness(1);
  filter: brightness(1);
  cursor: pointer;
}

#entrada .contenido p {
  min-width: 300px;
  max-width: 800px;
  top: 500px;
  left: 100px;
  font-size: 14pt;
}

#entrada .contenido .playBtn {
  top: calc(50% - 100px);
  left: 50%;
  width: 100px;
  height: 100px;
  border: 1px solid white;
  border-radius: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
}

#entrada iframe {
  width: 500px;
  height: 300px;
  position: absolute;
  top: 150px;
  left: 100px;
}

#entrada .cerrarBtn, #entrada .galeriaBtn {
  width: 30px;
  height: 30px;
  position: fixed;
  cursor: pointer;
}

#entrada .cerrarBtn {
  left: 15px;
  top: 35px;
  z-index: 2;
  pointer-events: none;
}

#entrada .cerrarBtn::before, #entrada .cerrarBtn::after {
  -webkit-transition: all 300ms ease;
  -o-transition: all 300ms ease;
  transition: all 300ms ease;
  content: "";
  display: block;
  position: absolute;
  width: 2px;
  height: 100%;
  background: black;
  left: 50%;
  top: 0;
  -webkit-transform: rotate(45grad);
  -ms-transform: rotate(45grad);
  transform: rotate(45grad);
  -webkit-transform-origin: center;
  -ms-transform-origin: center;
  transform-origin: center;
}

#entrada .cerrarBtn::after {
  -webkit-transform: rotate(-45grad);
  -ms-transform: rotate(-45grad);
  transform: rotate(-45grad);
}

#entrada .cerrarBtn:hover::after, #entrada .cerrarBtn:hover::before {
  -webkit-transform-origin: center bottom;
  -ms-transform-origin: center bottom;
  transform-origin: center bottom;
}

#entrada .galeriaBtn {
  -webkit-transition: all 500ms ease;
  -o-transition: all 500ms ease;
  transition: all 500ms ease;
  right: 50px;
  top: 20px;
  height: 80px;
  width: 80px;
}

#entrada .galeriaBtn::before {
  content: "";
  display: block;
  -webkit-transition: all 500ms ease;
  -o-transition: all 500ms ease;
  transition: all 500ms ease;
  position: absolute;
  top: 50%;
  left: 40%;
  height: 1px;
  width: 20px;
  background: white;
  opacity: 0;
}

#entrada .galeriaBtn svg {
  stroke: white;
  fill: none;
  stroke-width: 1px;
  width: 100%;
  height: 100%;
}

#entrada .galeriaBtn svg * {
  vector-effect: non-scaling-stroke;
}

#entrada .galeriaBtn:hover {
  right: 70px;
}

#entrada .galeriaBtn:hover::before {
  opacity: 1;
  left: 105%;
  width: 50px;
}

#entrada .titulo {
  position: fixed;
  top: 20px;
  left: 0;
  height: auto;
  text-align: right;
  line-height: 2em;
  padding-right: 1em;
  padding-left: 100px;
  background-color: white;
  color: black;
}



